::selection {
    background: #ffe57f;
}

:root {
    --base-size: 45px
}

.xl {
    font-size: calc(var(--base-size) * 1.0);
}

.l {
    font-size: calc(var(--base-size) * 0.75);
}

.m {
    font-size: calc(var(--base-size) * 0.55);
}

.s {
    font-size: calc(var(--base-size) * 0.40);
}

.xs {
    font-size: calc(var(--base-size) * 0.3);
}

/*vertical fonts*/

@media only screen and (max-height: 550px) {
    .xl {
        font-size: calc(var(--base-size) * 1.0 * 0.8);
    }

    .l {
        font-size: calc(var(--base-size) * 0.75  * 0.8);
    }

    .m {
        font-size: calc(var(--base-size) * 0.55  * 0.8);
    }

    .s {
        font-size: calc(var(--base-size) * 0.40  * 0.8);
    }

    .xs {
        font-size: calc(var(--base-size) * 0.3  * 0.8);
    }
}

